body {
  margin: 0;
  width: 100%;
  height:100%;
  font-family:Poppins, Approach, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue";

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:white;
  margin:0;
  padding:0;
  background-image: linear-gradient(45deg,rgba(23, 20, 211, 0.815),rgb(29, 208, 240));
 
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
